<template>
  <div id="loginManagement">
    <div class="queryBox">
      <QueryBox :formArr="queryFrom" @query="getQueryData" style="width: 100%" ref="getQuery" id="logQuery"></QueryBox>
    </div>
    <div class="content_box">
      <div>
        <Table border stripe :loading="loading" :columns="columns1" :data="data1" highlight-row size="small" :max-height="tableHeight">
        </Table>
        <div class="page_box" id="pageBox">
          <Page :total="total" ref="pages" :page-size="pageSize" :current.sync="pageNum" size="small" @on-change="pageChange" show-total show-elevator/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import QueryBox from '@/components/publicQuery'
import {
  // 表格列表
  getLoginList
} from '@/api/systemManage'
import { renderDateTime } from '@/utils/common'
export default {
  name: 'logManagement',
  data () {
    return {
      /* 表格高度 */
      tableHeight: window.innerHeight - 290,
      /* 查询框查询条件 */
      queryData: {},
      /* 表格数据 */
      columns1: [
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.pageNum - 1) * this.pageSize + 1)
          }
        },
        {
          title: '登录人名称',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          minWidth: 200,
          key: 'login_name'
        },
        {
          title: '登录时间',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          minWidth: 200,
          key: 'create_time',
          render: (h, params) => {
            return h('span', renderDateTime(params.row.create_time))
          }
        },
        {
          title: '登录设备类型',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          minWidth: 200,
          key: 'type',
          render: (h, params) => {
            if (params.row.type === 0) {
              return h('span', 'pc端')
            } else if (params.row.type === 1) {
              return h('span', '移动端')
            }
          }
        },
        {
          title: '登录IP',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          minWidth: 200,
          key: 'login_ip'
        },
        {
          title: '登录类型',
          align: 'center',
          // ellipsis: true,
          // tooltip: true,
          minWidth: 200,
          key: 'login_type',
          render: (h, params) => {
            if (params.row.login_type === '0') {
              return h('span', '登入')
            } else if (params.row.login_type === '1') {
              return h('span', '登出')
            }
          }
        }
      ],
      data1: [],
      /* 总条数 */
      total: 0,
      pageNum: 1,
      pageSize: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 加载 */
      loading: false
    }
  },
  components: {
    QueryBox
  },
  computed: {
    // 查询表单参数
    queryFrom: function () {
      return [
        {
          type: 1,
          field: 'name',
          title: '登录人名称'
        }
      ]
    }
  },
  created () {
    this.getTableList()
  },
  mounted () {
    // 窗口改变时table自适应
    window.addEventListener('resize', () => {
      this.tableHeight = window.innerHeight - 290
      this.pageSize = Math.floor((window.innerHeight - 290) / 40) - 1
      this.getTableList()
    }, false)
  },
  methods: {
    /* 获取查询数据 */
    getQueryData (data) {
      this.queryData = data
      this.pageNum = 1
      this.getTableList()
    },
    /* 获取字段列表 */
    getTableList () {
      let name
      if (this.queryData.name === undefined) {
        name = ''
      } else {
        name = this.queryData.name
      }
      this.loading = true
      getLoginList({
        data: {
          pageSize: this.pageSize,
          pageNum: this.pageNum,
          name
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.data1 = res.data.data.poList
          this.total = res.data.data.poSum
          this.loading = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 分页跳转 */
    pageChange (pageNum) {
      this.pageNum = pageNum
      this.getTableList()
    }
  }
}
</script>

<style scoped lang="scss">
  #loginManagement {
    position: relative;
    .iconfont {
      margin-right: 4px;
    }
    .queryBox {
      height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      position: relative;
      overflow: inherit;
      .queryBtns {
        float: right;
        margin-top: 14px;
      }
    }
    .content_box {
      margin: 0 15px 0;
      .page_box{
        width: 100%;
        padding: 15px;
        text-align: right;
      }
    }
  }
</style>
